import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import personTop from '../components/personTop'
import personContent from '../components/personContent'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
        path:'/top',
        name:'person-top',
        component:personTop
    },
    {
        path:'/content',
        name:'person-content',
        component:personContent
    }
  ]
})



